:root {
  --color-grey: #cecece;
}

.jsa-theme-minimal dt {
  display: flex;
  align-items: center;
  position: relative;
  border-top: 1px solid var(--color-grey);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
}
.jsa-theme-minimal dt.active {
  border-bottom: none;
  border-top: 1px solid #e3e3e3;
}
.jsa-theme-minimal dt:first-of-type {
  border-top: 1px solid #e3e3e3;
}
.jsa-theme-minimal dt:last-of-type.active {
  border-bottom: none;
}
.jsa-theme-minimal dt a {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 600;
  text-decoration: none;
  width: 100%;
  padding: 15px 30px;
}
.jsa-theme-minimal dt a[aria-expanded=true]::after {
  content: "-";
}
.jsa-theme-minimal dt a::after {
  content: "+";
  height: 18px;
  width: 18px;
  position: absolute;
  right: 30px;
  vertical-align: middle;
  display: inline-block;
}
.jsa-theme-minimal dd {
  max-height: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  overflow: hidden;
  padding: 0;
  margin: 0;
}
.jsa-theme-minimal dd.active {
  border-bottom: 1px solid var(--color-grey);
  padding: 2em;
  max-height: 100%;
}
.jsa-theme-minimal dd div {
  padding: 0 2rem 2rem 2rem;
}

.jsa-theme-accordion dt {
  display: flex;
  align-items: center;
  position: relative;
  /* cursor: pointer; */
  background-color: #fafafa;
  border-bottom: 1px solid #e3e3e3;
  border-left: 1px solid #e3e3e3;
  border-right: 1px solid #e3e3e3;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
}
.jsa-theme-accordion dt.active {
  border-bottom: none;
  border-top: none;
}
.jsa-theme-accordion dt:first-of-type {
  border-top: 1px solid #e3e3e3;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
.jsa-theme-accordion dt:last-of-type {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  border-bottom: 1px solid #e3e3e3;
}
.jsa-theme-accordion dt:last-of-type.active {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom: none;
}
.jsa-theme-accordion dt:last-of-type.active + dd {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.jsa-theme-accordion dt a {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 600;
  text-decoration: none;
  width: 100%;
  padding: 15px 30px;
}
.jsa-theme-accordion dt a[aria-expanded=true]::after {
  content: "-";
}
.jsa-theme-accordion dt a::after {
  content: "+";
  height: 18px;
  width: 18px;
  position: absolute;
  right: 30px;
  vertical-align: middle;
  display: inline-block;
}
.jsa-theme-accordion dd {
  max-height: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  overflow: hidden;
  padding: 0;
  margin: 0;
  transition: max-height 0.4s cubic-bezier(0.4, 0, 0.2, 1), padding 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.jsa-theme-accordion dd.show {
  border: 1px solid #e3e3e3;
  padding: 2em;
  max-height: 100%;
  transition: max-height 0.4s cubic-bezier(0.4, 0, 0.2, 1), padding 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}